<template>
  <div>
    <textarea :class="warning" name="" id="" cols="100" rows="20" v-model="text"></textarea>

    <br>

    <button @click="send">Send</button>

    <p>{{subjectProp}}</p>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  props:{
    subjectProp: String
  },
  data() {
    return {
      subject: '',
      text: '',
      teacher: '',
      axiosAdress: 'localhost:3000/',
      warning:'normal'
    }
  },
  methods: {
    selectTeacher(){
      switch(this.subjectProp){
        case "Design":
          this.teacher = 'Robin';
          break;
        case "Digitalt skapande":
          this.teacher = 'Robert';
          break;
        case "Idrott":
          this.teacher = 'Robert';
          break;
        case "Gränssnittsdesign":
          this.teacher = 'Thomas';
          break;
        case "Medieproduktion":
          this.teacher = 'Thomas';
          break;
        case "Historia":
          this.teacher = 'Hanna'
          break;
        case "Religion":
          this.teacher = 'Hanna'
          break;
        case "Engelska":
          this.teacher = 'ANKA'
          break;
        case "Svenska":
          this.teacher = 'Andreas'
          break;
        case "Webbutveckling":
          this.teacher = 'Niklas'
          break;
      }
    },
    send(){
      this.selectTeacher()
      if(this.text === ''){
        this.noText()
        return 'no text'
      }
      let payload = JSON.stringify({
          subject: this.subjectProp,
          text: this.text,
          teacher: this.teacher
        })
      axios.post('http://127.0.0.1:3000/setSubject',payload,{
        headers:{
          'Content-Type': 'application/json'
        },
      })
      .then(()=>{
        this.text = ''
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    noText(){
      this.warning = "warning"
    }
  },
  watch:{
    subjectProp(){
      if(this.subject !== this.subjectProp){
        this.warning = 'normal'
        this.subject = this.subjectProp
      }
    }
  }
}
</script>

<style scoped>
  textarea{
    resize:none;
    margin-bottom: 20px;
  }
  textarea:focus-visible{
    outline: 2px limegreen solid;
  }
  .normal{
    border: black solid 3px;
    animation:none
  }
  .warning{
    border: red solid 3px;
    animation: shake 2s;
  }
  @keyframes shake {
    20%   {transform: translateX(-10px);}
    40%   {transform: translateX(10px);}
    60%   {transform: translateX(-10px);}
    80%   {transform: translateX(10px);}
    100%  {transform: translateX(0px);}
  }
</style>